/* Scrollbar */
::-webkit-scrollbar {
    width: 3px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #121212;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #636363; 
    border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #545454; 
}

/* Skill Bar */
line {
  stroke-width: 6px;
  stroke-linecap: round;
}

.scrollable-content {
  overflow: auto;
}

rect {
  stroke-width: 3px;
  stroke-linecap: round;
  fill: transparent;
}

.about-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-y: hidden;
  padding: 20px;
}

@media (max-width: 768px) {
  .about-container {
    padding: 10px;
  }
}

.chrono-icons {
  display: none;
}

.chrono-timeline .chrono-icons {
  display: none;
}

.chrono-timeline .chrono-dates {
  color: #15bfe3;
}

.chrono-timeline .chrono-items {
  padding: 0 20px;
}

.timeline-container {
  width: 100%;
  overflow-x: auto;
  white-space: nowrap;
  pointer-events: auto; /* Enable pointer events for the timeline container */
}

.hexagon-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding-left: calc(var(--hexagon-width) / 3);
  margin-bottom: calc(var(--hexagon-height) / 3);
}

.hexagon {
  position: relative;
  width: var(--hexagon-width);
  height: var(--hexagon-height);
  margin: var(--hexagon-margin);
  display: inline-block;
  cursor: pointer;
  clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
  transition: transform 0.3s;
}

.hexagon::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  clip-path: inherit;
  opacity: 0;
  transition: opacity 0.3s;
  z-index: 1; /* Ensure the overlay is above the image */
}

.hexagon:hover::before {
  opacity: 1;
}

.hexagon img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  clip-path: inherit;
  position: absolute;
  top: 0;
  left: 0;
}

.hexagon-title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;
  text-align: center;
  opacity: 0;
  transition: opacity 0.3s;
  z-index: 2; /* Ensure the text is above the overlay */
}

.hexagon:hover .hexagon-title {
  opacity: 1;
}

.card-detailed-text {
  white-space: normal;
  word-wrap: break-word;
  overflow-wrap: break-word;
}

:root {
  --hexagon-width: 150px;
  --hexagon-height: 173px;
  --hexagon-margin: 2px;
}

